/*!
 * table - 0.1.0 */
.br-table-title {
  border-bottom: 2px solid #cccccc;
  color: #555555;
  font-size: 20px;
  font-weight: 500;
  margin-bottom: 40px;
  padding-bottom: 10px;
}

.br-table-responsive {
  display: block;
  overflow-x: auto;
  width: 100%;
}

.br-table {
  width: 100%;
}

.br-table thead {
  background: #f8f8f8;
}

.br-table thead th {
  border-bottom: 1px solid #cccccc;
  border-top: 1px solid #cccccc;
  color: #0c326f;
  padding: 30px 25px;
  text-align: center;
}

.br-table tbody tr {
  border-bottom: 1px solid #cccccc;
}

.br-table tbody tr:hover {
  background: #dbe8fb;
  cursor: pointer;
}

.br-table tbody tr th,
.br-table tbody tr td {
  color: #555555;
  padding: 20px 25px;
}

.br-table tbody tr th i,
.br-table tbody tr td i {
  color: #1351b4;
  cursor: pointer;
  margin: 0 8px;
}

.br-table tbody tr.active, .br-table tbody tr:active, .br-table tbody tr:focus {
  background: #2b74e9;
}

.br-table tbody tr.active i,
.br-table tbody tr.active th,
.br-table tbody tr.active td, .br-table tbody tr:active i,
.br-table tbody tr:active th,
.br-table tbody tr:active td, .br-table tbody tr:focus i,
.br-table tbody tr:focus th,
.br-table tbody tr:focus td {
  color: #ffffff;
}

.br-table tbody tr.show-table {
  display: table-row;
}

.br-table tbody tr.show-table th {
  display: none;
}

.br-table tbody tr.hide-table {
  display: none;
}

@media (max-width: 576px) {
  .br-table-responsive {
    display: block;
    overflow-x: auto;
    width: 100%;
  }
  .br-table tbody tr:first-child th:first-child {
    background: #f8f8f8;
    border-bottom: 1px solid #cccccc;
    box-shadow: 4px 0px 2px -1px rgba(0, 0, 0, 0.2);
    margin-top: 1px;
  }
  .br-table {
    margin-left: 150px;
  }
  .br-table thead tr th:first-child,
  .br-table thead tr td:first-child,
  .br-table tbody tr th:first-child,
  .br-table tbody tr td:first-child {
    background: #f8f8f8;
    border: 0;
    border-bottom: 1px solid #cccccc;
    box-shadow: 4px 0px 2px -1px rgba(0, 0, 0, 0.2);
    left: 0;
    position: absolute;
    width: 150px;
    z-index: 2;
  }
  .br-table thead tr th.active,
  .br-table thead tr td.active,
  .br-table tbody tr th.active,
  .br-table tbody tr td.active {
    background: #2b74e9;
    border-color: #2b74e9;
  }
  .br-table thead.show-table th,
  .br-table tbody.show-table th {
    display: table-cell;
  }
}

.br-table thead tr th:first-child {
  border-bottom: 1px solid #cccccc;
  border-top: 1px solid #cccccc;
  margin-top: 0;
}

.br-table tbody tr th:first-child i,
.br-table tbody tr th:first-child input[type="checkbox"] {
  margin-top: 16px;
}
